@import "styles/colorPalette.scss";

.GeneralInformationField {
  display: grid;
  width: 100%;
  padding-left: 2.5em;
  height: auto;
  grid-template-rows: 0.2fr 1.8fr;
  flex-direction: row;
  grid-auto-rows: minmax(100%, auto);

  font-weight: 500;
  color: $color-12;

  .generalInfoText {
    width: 100%;
    height: 50px;
    color: $color-12;
    font-weight: 400;
    font-size: 24px;
  }

  .generalInfoFields {
    display: grid;
    grid-template-columns: 0.4fr 1fr;
    grid-auto-rows: minmax(100%, auto);
    flex-direction: column;

    .firstPartField {
      display: flex;
      flex-direction: column;
      width: 100%;

      .InputContainer {
        width: 90%;
      }

      .InputContainerTwo {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        width: 90%;

        .suffix {
          width: 35%;
          height: auto;
          margin-right: 7%;
        }
        .birthDate {
          width: 65%;
          height: auto;

          > label {
            margin-bottom: 1em;
          }
          > div {
            margin-top: 0.35em;
            width: 100%;
            height: auto;
          }
        }
      }

      .InputContainerDropDown {
        margin-bottom: 1.6em;
        width: 90%;
      }
    }

    .secondPartField {
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: column;

      .textField {
        width: 35%;
      }
      .DropDowntextField {
        width: 35%;
        margin-bottom: 1.65em;
      }

      .firstTextField {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: auto;

        .middleName {
          width: 35%;
          height: 100%;
          margin-right: 2.5em;
        }
        .lastName {
          width: 35%;
          height: 100%;
        }
      }

      .secondTextField {
        display: flex;
        flex-direction: row;
        // margin-bottom: 1.6em;

        .civilStatus {
          width: 16%;
          margin-right: 3%;
        }
        .gender {
          width: 16%;
        }
      }

      .ptrNumberAndS2Number {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: auto;

        > div:nth-child(1) {
          width: 35%;
          height: 100%;
          margin-right: 2.5em;
        }

        > div:nth-child(2) {
          width: 35%;
          height: 100%;
        }
      }

      .textFieldforPRCfield {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: auto;
        padding: 0;
        color: $color-12;

        > div:nth-child(1) {
          width: 21.5%;
          margin-right: 2.1em;
          margin-top: 0.3em;
          height: auto;
        }
        > div:nth-child(2) {
          width: 25.5%;
          margin-right: 2em;
        }
        > div:nth-child(3) {
          width: 20.1%;
          margin-top: 0.3em;
        }
      }
    }
  }
}
