@import "styles/colorPalette.scss";

.Wrapper {
  padding-left: 2.5em;
  .dentalAssociateInformation {
    display: grid;
    margin-top: 2.3em;
    grid-template-rows: 0.3fr 1fr 1fr;
    flex-direction: row;
    grid-auto-rows: minmax(100%, auto);
    grid-gap: 0.5em;
    color: $color-12;

    .dentalAssociateGenInfo {
      font-size: 24px;
      font-weight: 500;
      width: 100%;
      height: 40px;
      margin-bottom: 1em;
    }

    .dentalAssocInfoFirstInputFields,
    .dentalAssocInfoSecondInputFields {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      padding: 0;
      width: 100%;
      // height: 100%;

      .fn,
      .cn,
      .PRC {
        width: 26.5%;
        margin-right: 2.5em;
      }
      .ms {
        width: 26.5%;
        padding-bottom: 1.6em;
      }
      .PRCED {
        width: 13.5%;
        margin-right: 2.5em;
        padding-top: 0.5em;
      }
      .addTo {
        user-select: none;
        > label {
          text-decoration: underline;
          color: $color-9;
          &:hover {
            cursor: pointer;
            font-weight: bold;
          }
        }
      }
    }
  }
  .tableFields {
    width: 85%;
    // height: 100%;
    padding: 0 1em 1em 0;
    margin-top: 0;
  }
  .affiliationTableFields {
    width: 100%;
    // height: auto;
    display: flex;
    flex-direction: column;
    padding-bottom: 1em;

    .AffiliaionText {
      font-size: 24px;
      font-weight: 500;
      color: $color-12;
      margin-bottom: 1em;
    }

    .tableDropDownInput {
      width: 60%;
      height: 100%;

      .textDesciption {
        color: $color-12;
      }
    }
  }
}
