@import "styles/colorPalette.scss";

.firstPartInputFields {
  display: grid;
  flex-direction: row;
  grid-template-rows: repeat(4, 1fr);
  grid-auto-rows: minmax(auto, 100%);
  padding-top: 1em;
  padding-bottom: 1em;
  grid-gap: 1em;
  color: $color-12;

  > div:nth-child(1) {
    display: flex;
    flex-direction: column;
    width: 66%;
  }

  .firstLayerFields {
    display: flex;
    flex-direction: row;
    > div {
      margin-right: 3.5em;
    }
    > div:nth-child(1) {
      width: 27%;
      margin-right: 3.4em;
    }
    > div:nth-child(2) {
      width: 30.5%;

      > div {
        display: flex;
        flex-direction: row;
        // justify-content: space-between;
        width: 100%;
        height: auto;

        > div:nth-child(1) {
          width: 40%;
          margin-right: 8px;
          .customTag {
            color: $color-12;
            opacity: 0.5;
            font-size: 0.9vw;
            margin-left: 0.6em;
            margin-right: 0.6em;
          }
        }

        > div:nth-child(2) {
          width: 40%;
          display: flex;
          .customTag {
            color: $color-12;
            font-size: 0.9vw;
            opacity: 0.5;
            margin-left: 0.3em;
            margin-right: 0.3em;
          }
          > label {
            color: white;
          }
        }
      }
    }
  }

  .secondLayerFields {
    display: flex;
    flex-direction: row;
    > div {
      margin-right: 3.5em;
    }

    > div:nth-child(1) {
      width: 27%;
    }
    > div:nth-child(2) {
      width: 15%;
    }
    > div:nth-child(3) {
      width: 25.5%;
      overflow: visible;
      > label {
        overflow: visible;
      }
      > div {
        width: 60.5%;
        margin-top: 0.5em;
      }
    }
  }

  .thirdLayerFields {
    display: flex;
    flex-direction: row;
    > div {
      margin-right: 3.5em;
    }

    > div:nth-child(1) {
      width: 27%;
    }
    > div:nth-child(2) {
      width: 15.5%;
      > div {
        margin-top: 0.4em;
      }
    }
    > div:nth-child(3) {
      width: 22%;
      margin-top: 0.05em;
    }
  }
}
